import Nav from "./Nav";
import Header from "./header";
import Menu from "./menu";
const Layout = ({ children, mainTitle, canSigle, footer }) => (
  <body
    id="top"
    data-spy="scroll"
    data-target=".navbar-collapse"
    data-offset="50"
  >
    <Header
      canInfo={{
        canSigle: "Welcome to Centre Ambulancier National(CAN)",
        canTel: "3711-01-10",
        canHoraire: "6:00 AM - 6:00 PM (Mon-Fri)",
        canMail: "cann116@mspp.gouv.ht",
      }}
    />

<section class="navbar navbar-default navbar-static-top" role="navigation">
      <div class="container">
        <div class="navbar-header">
          <button
            class="navbar-toggle"
            data-toggle="collapse"
            data-target=".navbar-collapse"
          >
            <span class="icon icon-bar"></span>
            <span class="icon icon-bar"></span>
            <span class="icon icon-bar"></span>
          </button>

          {/* lOGO TEXT HERE  <a  href="index.html" class="navbar-brand ">CAN116</a> */}
          <img src="/static/can-logo.png" style={{ height: "90px" }} />
        </div>

        {/* MENU LINKS */}
        <div class="collapse navbar-collapse">
          <ul class="nav navbar-nav navbar-right">
          <li><a href="#top" class="smoothScroll">Home</a></li>
            <li>
              <a href="#top" class="smoothScroll">
                Home
              </a>
            </li>
            <li>
              <a href="#about" class="smoothScroll">
                About Us
              </a>
            </li>
            <li>
              <a href="#team" class="smoothScroll">
                Doctors
              </a>
            </li>
            <li>
              <a href="#news" class="smoothScroll">
                News
              </a>
            </li>
            <li>
              <a href="#google-map" class="smoothScroll">
                Contact
              </a>
            </li>
            <li class="appointment-btn">
              <a href="#appointment">Make an appointment</a>
            </li>
          </ul>
        </div>
      </div>
    </section>

 

    <hr />
    {children}

    {/* <h1>{footer}</h1> */}
  </body>
);

export default Layout;
